import { Link } from '@reach/router';
import { HeadFC, PageProps } from 'gatsby';
import React from 'react';
import styled from 'styled-components';
import Layout from '../components/layout';
import Seo from '../components/seo';

interface PageContextProps {
  tags: {
    tag: string;
    totalCount: number;
  }[];
}

const TagEl = styled.h2`
  margin: 1em 0.1em;
  font-size: 20px;
`;

const LinkText = styled(Link)`
  color: var(--font-color);
  text-decoration: none;

  transition: 0.08s cubic-bezier(0.9, 0.03, 0.31, 1.36);
  &:visited {
    color: var(--font-color);
  }
  &:hover {
    /* animation: link-line 0.1s ease-in; */
    color: var(--content-color);
  }
`;

const Tag: React.FC<{ tag: string; num: number }> = ({ tag, num }) => {
  return (
    <TagEl>
      <LinkText to={`/tag/${tag}`}>
        {tag} â€¢ {num}
      </LinkText>
    </TagEl>
  );
};

const Tags = ({ pageContext }: PageProps<{}, PageContextProps>) => {
  return (
    <Layout pageTitle="Tags">
      <div>
        {pageContext.tags.map((tag) => (
          <Tag key={tag.tag} tag={tag.tag} num={tag.totalCount} />
        ))}
      </div>
    </Layout>
  );
};

export const Head: HeadFC = () => <Seo title="Tags" />;

export default Tags;
